@import url(https://fonts.googleapis.com/css?family=Lato:400,300,300italic,400italic,700,700italic);
/**
*** SIMPLE GRID
*** (C) ZACH COLE 2016
**/
/* UNIVERSAL */
html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  left: 0;
  top: 0;
  font-size: 100%;
}
/* ROOT FONT STYLES */
* {
  font-family: 'Lato', Helvetica, sans-serif;
  color: #333447;
  line-height: 1.5;
}
/* TYPOGRAPHY */
h1 {
  font-size: 2.5rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.375rem;
}
h4 {
  font-size: 1.125rem;
}
h5 {
  font-size: 1rem;
}
h6 {
  font-size: 0.875rem;
}
p {
  font-size: 1.125rem;
  font-weight: 200;
  line-height: 1.8;
}
.font-light {
  font-weight: 300;
}
.font-regular {
  font-weight: 400;
}
.font-heavy {
  font-weight: 700;
}
/* POSITIONING */
.left {
  text-align: left;
}
.right {
  text-align: right;
}
.center {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}
.justify {
  text-align: justify;
}
/* ==== GRID SYSTEM ==== */
.container {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}
.row {
  position: relative;
  width: 100%;
}
.row [class^="col"] {
  float: left;
  margin: 0.5rem 2%;
  min-height: 0.125rem;
}
.col-1,
.col-2,
.col-3,
.col-4,
.col-5,
.col-6,
.col-7,
.col-8,
.col-9,
.col-10,
.col-11,
.col-12 {
  width: 96%;
}
.col-1-sm {
  width: 4.33%;
}
.col-2-sm {
  width: 12.66%;
}
.col-3-sm {
  width: 21%;
}
.col-4-sm {
  width: 29.33%;
}
.col-5-sm {
  width: 37.66%;
}
.col-6-sm {
  width: 46%;
}
.col-7-sm {
  width: 54.33%;
}
.col-8-sm {
  width: 62.66%;
}
.col-9-sm {
  width: 71%;
}
.col-10-sm {
  width: 79.33%;
}
.col-11-sm {
  width: 87.66%;
}
.col-12-sm {
  width: 96%;
}
.row::after {
	content: "";
	display: table;
	clear: both;
}
.hidden-sm {
  display: none;
}
@media only screen and (min-width: 33.75em) {  /* 540px */
.container {
    width: 80%;
}
}
@media only screen and (min-width: 45em) {  /* 720px */
.col-1 {
    width: 4.33%;
}
.col-2 {
    width: 12.66%;
}
.col-3 {
    width: 21%;
}
.col-4 {
    width: 29.33%;
}
.col-5 {
    width: 37.66%;
}
.col-6 {
    width: 46%;
}
.col-7 {
    width: 54.33%;
}
.col-8 {
    width: 62.66%;
}
.col-9 {
    width: 71%;
}
.col-10 {
    width: 79.33%;
}
.col-11 {
    width: 87.66%;
}
.col-12 {
    width: 96%;
}
.hidden-sm {
    display: block;
}
}
@media only screen and (min-width: 60em) { /* 960px */
.container {
    width: 75%;
    max-width: 60rem;
}
}
#app {
  font-family: 'Roboto', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  color: white;
}
html,
body {
  overflow-x: hidden;
  width: 100%;
  background-color: #1a171b;
}
body {
  color: #fff;
  letter-spacing: 0.2px;
}
body.search-active {
    overflow: hidden;
    opacity: 1;
}
body.search-active .search-input {
      opacity: 1;
      -webkit-transform: none;
              transform: none;
      pointer-events: all;
}
body.search-active .icon-close {
      opacity: 1;
      -webkit-transform: rotate(-90deg);
              transform: rotate(-90deg);
}
body.search-active .control {
      cursor: default;
}
body.search-active .control .btn-material {
        -webkit-transform: scale(70);
                transform: scale(70);
}
body.search-active .control .icon-material-search {
        opacity: 0;
}
.icon-close {
  position: fixed;
  top: 30px;
  right: 30px;
  color: #fff;
  cursor: pointer;
  font-size: 70px;
  opacity: 0;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.icon-close:hover {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
.icon-close {
      font-size: 48px;
}
}
.search-input {
  height: 80px;
  position: absolute;
  top: 50%;
  pointer-events: none;
  opacity: 0;
  -webkit-transform: translate(40px, 0);
          transform: translate(40px, 0);
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.search-input input {
    color: #fff;
    font-size: 54px;
    border: 0;
    background: transparent;
    -webkit-appearance: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    outline: 0;
    font-weight: 200;
}
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
.search-input input {
        font-size: 32px;
}
}
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
.search-input {
      top: 5%;
}
}
.search-input ::-webkit-input-placeholder {
    /* WebKit, Blink, Edge */
    color: rgba(255, 255, 255, 0.4);
}
.search-input :-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: rgba(255, 255, 255, 0.4);
}
.search-input ::-ms-input-placeholder {
    /* Microsoft Edge */
    color: rgba(255, 255, 255, 0.4);
}
.search-input ::placeholder {
    /* Most modern browsers support this now. */
    color: rgba(255, 255, 255, 0.4);
}
.control {
  cursor: pointer;
  margin: 0 auto;
}
.control .btn-material {
    width: 60px;
    height: 60px;
    border-radius: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background: #f57e1f;
    outline: 0;
    padding: 20px;
    -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
            box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    -webkit-transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}
.control .btn-material:hover {
      -webkit-box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
              box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}
.control .icon-material-search {
    color: #fff;
    -webkit-transition: opacity 0.3s ease-in-out;
    transition: opacity 0.3s ease-in-out;
}
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
ul li {
    display: inline-block;
    margin: 0 10px;
}
a {
  color: #42b983;
}
